import repository from './repository'

const API_URL = '/api/movies'

export default {
  list (page) {
    return repository.get(API_URL + '/upcoming', { params: { page: page } })
  },
  get (movieId) {
    return repository.get(API_URL + '/' + movieId)
  },
  search (text) {
    return repository.get(API_URL + '/search', { params: { query: text } })
  },
  genres (ids) {
    // TODO create API fetch and then cache the results
    return [{
      'id': 28,
      'name': 'Action'
    }, {
      'id': 12,
      'name': 'Adventure'
    }, {
      'id': 16,
      'name': 'Animation'
    }, {
      'id': 35,
      'name': 'Comedy'
    }, {
      'id': 80,
      'name': 'Crime'
    }, {
      'id': 99,
      'name': 'Documentary'
    }, {
      'id': 18,
      'name': 'Drama'
    }, {
      'id': 10751,
      'name': 'Family'
    }, {
      'id': 14,
      'name': 'Fantasy'
    }, {
      'id': 36,
      'name': 'History'
    }, {
      'id': 27,
      'name': 'Horror'
    }, {
      'id': 10402,
      'name': 'Music'
    }, {
      'id': 9648,
      'name': 'Mystery'
    }, {
      'id': 10749,
      'name': 'Romance'
    }, {
      'id': 878,
      'name': 'Science Fiction'
    }, {
      'id': 10770,
      'name': 'TV Movie'
    }, {
      'id': 53,
      'name': 'Thriller'
    }, {
      'id': 10752,
      'name': 'War'
    }, {
      'id': 37,
      'name': 'Western'
    }]
  }
}



